* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  color: #000;
}

/* Global classes */

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.clearfix:before,
.clearfix:after {
  content: '';
  display: table;
  clear: both;
}

.clear {
  clear: both;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  overflow: hidden;
  margin-top: -1px;
  margin-left: -1px;
  position: absolute;
  clip: rect(0, 0, 0, 0);
  border: none;
}

.btn {
  background-color: transparent;
  width: 248px;
  height: 65px;
  line-height: 65px;
  display: inline-block;
  text-align: center;
  color: #000000;
  font-weight: 600;
  border: 2px solid #000000;
  text-decoration: none;
  cursor: pointer;
}

/* Atomic classes */

.d-n {
  display: none;
}

.d-b {
  display: block;
}

/* General */

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 70px;
}

.navigation__ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
}

.navigation__ul.isOpened {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 4;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
}

.navigation__li {
  margin-bottom: 40px;
}

.navigation__a {
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 600;
}

.navigation__b {
  margin-top: 80px;
}

.navigation__b_a {
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 600;
}

.navigation__b_a:before {
  content: '';
  background-image: url('../img//svg/ico-phone.svg');
  width: 15px;
  height: 15px;
  background-repeat: no-repeat;
  background-position: center;
  display: inline-block;
}

.navigation__b_p {
  color: #FFFFFF;
  text-decoration: underline;
  margin-top: 20px;
  text-align: center;
}

.mobile-menu {
  display: block;
  width: 30px;
  height: 16px;
  margin-top: 14px;
  background-color: transparent;
  border: none;
  border-top: 2px solid #000000;
  position: relative;
  outline: none;
}

.mobile-menu.closer {
  position: fixed;
  z-index: 5;
  outline: none;
  top: 20px;
  right: 20px;
  border: none;
  transition: 1s;
}

.mobile-menu.closer:before,
.mobile-menu.closer:after {
  content: '';
  display: block;
  min-width: 100%;
  position: absolute;
  border: 1px solid #ffff;
  outline: none;
  transform-origin: 62% 62%;
  transition: 0.5s;
}

.mobile-menu.closer:before {
  transform: rotate(-45deg);
}

.mobile-menu.closer:after {
  transform: rotate(45deg);
}

.mobile-menu:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 25px;
  transform: translate(0, -100%);
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  border-bottom: 2px solid #000000;
}

.mobile-menu:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 20px;
  transform: translate(0, -50%);
  border-bottom: 2px solid #000000;
}

.catalog {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.catalog-paper__h {
  font-weight: 400;
  margin-bottom: 12px;
}

.catalog-paper__p {
  font-size: 14px;
  margin-bottom: 33px;
}

.catalog-paper__btn_a {
  margin-bottom: 85px;
}

.catalog-img {
  overflow: hidden;
}

.catalog-img__1 {
  position: relative;
  top: 70px;
  left: 20px;
}

.catalog-img__2 {
  position: relative;
  top: 50px;
  left: 80px;
}

.catalog-img__3 {
  position: relative;
  z-index: -1;
  top: -60px;
}

.catalog-img__4 {
  position: relative;
  z-index: -2;
  top: -100px;
  left: 20px;
}

.catalog-svg {
  align-self: center;
  list-style: none;
}

.catalog-svg__li {
  font-size: 10px;
  position: relative;
  margin: 60px 0;
}

.catalog-svg__li:before {
  content: '';
  position: absolute;
  width: 31px;
  height: 35px;
  top: -40px;
  left: 0;
}

.catalog-svg__li_choice:before {
  content: '';
  background-image: url(../img/svg/ico-choice.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.catalog-svg__li_price:before {
  content: '';
  background-image: url(../img/svg/ico-price.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.catalog-svg__li_exclusive:before {
  content: '';
  background-image: url(../img/svg/ico-exclusive.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.decorazza {
  margin-bottom: 105px;
  border-top: 1px solid transparent;
  width: 100%;
  height: 450px;
  background: url(../img/decor.jpg);
}

.decorazza-form {
  text-align: center;
  color: #FFFFFF;
  margin-top: 100px;
}

.decorazza-form__p:nth-child(1) {
  margin-bottom: 20px;
}

.decorazza-form__h {
  margin-bottom: 45px;
}

.decorazza-form__p:nth-child(3) {
  margin-bottom: 20px;
}

.figure__img {
  min-width: 100%;
}

@media screen and (min-width: 10px) {
  .navigation__ul {
    display: none;
  }
}

@media screen and (min-width: 560px) {
  .catalog-img {
    width: calc(66.66666666666667% - 20px);
  }

  .catalog-svg {
    width: calc(33.333333333333336% - 20px);
  }
}

@media screen and (min-width: 780px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}